<template>
  <div class="registerview">
    <div class="bg">
      <img src="../assets/img/t6.png" alt="" />
    </div>
    <div class="registertop">
      <router-link
        tag="span"
        to="/loginview"
        class="iconfont icon-xiangzuojiantou"
      ></router-link>
    </div>
    <h1>注册</h1>
    <div class="registerInfo">
      <div class="area">
        <p>中国大陆 +86</p>
        <span class="iconfont icon-jinrujiantou"></span>
      </div>
      <div class="phone">
        <input
          type="text"
          placeholder="请输入手机号"
          v-model="phone"
          @blur="checkPhone"
        />
        <p class="tips" v-if="!isPhonePass">请输入正确手机号</p>
      </div>
      <div class="nickname">
        <input
          type="text"
          placeholder="请输入昵称"
          v-model="nickname"
          @blur="checkNickname"
        />
        <p class="tips" v-if="!isNicknamePass">该昵称已被占用</p>
      </div>
      <div class="pwd">
        <input type="password" placeholder="请输入密码" v-model="pwd" />
      </div>
      <div class="checkpwd">
        <input
          type="password"
          placeholder="请再次确认密码"
          v-model="checkpwd"
          @blur="checkDoublepwd"
        />
        <p class="tips" v-if="!isCheckpwdPass">两次输入的密码不同</p>
      </div>
    </div>
    <div class="box">
      <div class="canflex"></div>
      <div class="registerbtn" v-show="isPass" @click="userRegister">注 册</div>
      <div class="forbidregisterbtn" v-show="!isPass">注 册</div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      nickname: "",
      phone: "",
      pwd: "",
      checkpwd: "",
      isNicknamePass: true,
      isPhonePass: true,
      isCheckpwdPass: true,
    };
  },
  methods: {
    checkNickname() {
      // console.log(111);
      let userMsg = localStorage.getItem("userlist");
      if (!userMsg || this.nickname == "") {
        return;
      } else if (userMsg) {
        let userlist = JSON.parse(userMsg);
        let index = userlist.findIndex(
          (item) => item.nickname == this.nickname
        );
        // console.log(index)
        if (index != -1) {
          this.isNicknamePass = false;
        }else {
          this.isNicknamePass = true
        }
      }
    },
    checkPhone() {
      var reg_tel =
        /^(13[0-9]|14[01456879]|15[0-35-9]|16[2567]|17[0-8]|18[0-9]|19[0-35-9])\d{8}$/;
      if (!reg_tel.test(this.phone)) {
        this.isPhonePass = false;
      } else {
        this.isPhonePass = true;
      }
    },
    checkDoublepwd() {
      if (this.checkpwd == "") {
        return;
      }
      if (this.pwd == this.checkpwd) {
        this.isCheckpwdPass = true;
      } else {
        this.isCheckpwdPass = false;
      }
    },
    userRegister() {
      let userlist = [];
      let userMsg = localStorage.getItem("userlist");
      if (userMsg) {
        userlist = JSON.parse(userMsg);
        let index = userlist.findIndex((item) => item.phone == this.phone);
        if (index != -1) {
          this.$toast("该手机号已注册, 请直接登录");
          this.nickname = this.phone = this.pwd = this.checkpwd = '';
          return;
        }
      }
      userlist.push({
        phone: this.phone,
        nickname: this.nickname,
        pwd: this.pwd,
        avatar: "https://cs.xinpianchang.com/passport/default.jpg",
      });
      userlist = JSON.stringify(userlist)
      localStorage.setItem("userlist", userlist);

      this.$toast.success("注册成功");
      this.nickname = this.phone = this.pwd = this.checkpwd = '';
    },
  },
  computed: {
    isPass() {
      if (
        this.nickname != "" &&
        this.phone != "" &&
        this.pwd != "" &&
        this.checkpwd != "" &&
        this.isNicknamePass &&
        this.isPhonePass &&
        this.isCheckpwdPass
      ) {
        return true;
      }
      return false;
    },
  },
};
</script>

<style lang="less">
.registerview {
  width: 100%;
  height: 100%;
  .bg {
    width: 100%;
    position: fixed;
    left: 0;
    bottom: 0;
    z-index: -1;
    img {
      width: 100%;
    }
  }
  .registertop {
    height: 40px;
    line-height: 40px;
    padding-left: 12px;
    padding-right: 16px;
    display: flex;
    justify-content: space-between;
    .iconfont {
      font-weight: bold;
    }
    p {
      font-size: 15px;
    }
  }
  h1 {
    font-size: 28px;
    font-weight: bold;
    font-family: "黑体";
    margin-top: 28px;
    margin-left: 20px;
    line-height: 32px;
  }
  .registerInfo {
    margin: 0 20px;
    margin-top: 44px;
    .area {
      display: flex;
      justify-content: space-between;
      height: 58px;
      line-height: 58px;
      font-size: 15px;
      border-bottom: 1px solid #f5f5f5;
    }
    .nickname,
    .phone,
    .pwd,
    .checkpwd {
      .tips {
        font-size: 12px;
        color: red;
        line-height: 26px;
      }
    }
    input {
      width: 100%;
      height: 58px;
      line-height: 58px;
      background: none;
      outline: none;
      border: none;
      border-bottom: 1px solid #f5f5f5;
      font-size: 15px;
    }
  }
  .box {
    display: flex;
    justify-content: space-between;
    margin-top: 28px;
    .forbidregisterbtn {
      width: 92px;
      height: 38px;
      line-height: 38px;
      margin-right: 20px;
      text-align: center;
      border-radius: 19px;
      background-color: #e5e5e5;
      font-size: 14px;
      color: #fff;
      font-weight: bold;
    }
    .registerbtn {
      width: 92px;
      height: 38px;
      line-height: 38px;
      margin-right: 20px;
      text-align: center;
      border-radius: 19px;
      background-color: #e45e51;
      font-size: 14px;
      color: #fff;
      font-weight: bold;
    }
  }
}
</style>